@tailwind base;
@tailwind components;
@tailwind utilities;

/* Add these rules to ensure proper scrolling */
body, html {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
}

#root {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.video-comparison-container {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  overflow-y: auto;
}

.video-container {
  position: relative;
  height: 0;
  padding-bottom: 56.25%; /* 16:9 Aspect Ratio */
  overflow: hidden;
}

/* Enhanced video display container */
.video-display-container {
  position: relative;
  width: 100%;
  min-height: 400px;
  max-height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.video-display-container iframe {
  width: 100%;
  height: 100%;
  border: none;
}

/* Ensure the scrubber doesn't break layout on small screens */
@media (max-width: 768px) {
  .time-control-panel {
    flex-direction: column;
  }
  
  .time-control-panel .flex {
    flex-wrap: wrap;
  }
  
  .video-display-container {
    min-height: 300px;
  }
}

/* Add aspect ratio utilities if they're not included in Tailwind */
@layer utilities {
  .aspect-w-16 {
    position: relative;
    padding-bottom: calc(var(--tw-aspect-h) / var(--tw-aspect-w) * 100%);
    --tw-aspect-w: 16;
  }
  
  .aspect-h-9 {
    --tw-aspect-h: 9;
  }
  
  .aspect-w-16 > * {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
}

/* Loading spinner */
.loading-spinner {
  display: inline-block;
  width: 50px;
  height: 50px;
  border: 3px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  border-top-color: #3b82f6;
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Global styling */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
} 